import TableModel from "./models/index.js";

let tableInstance = new Map();

function resetInstance(id) {
  const currInstance = tableInstance[id];
  currInstance?.reset();
  tableInstance.delete(id);
}

export default {
  beforeMount: (el, binding, vNode) => {
    const { uid: id } = vNode.ctx;
    if (!tableInstance.has(id))
      tableInstance.set(id, new TableModel(el, binding.value || {}));
  },
  updated: (el, binding, vNode) => {
    const { uid: id } = vNode.ctx;
    if (!tableInstance.has(id))
      tableInstance.set(id, new TableModel(el, binding.value || {}));
  },
  unmounted: (el, binding, vNode) => {
    const { uid: id } = vNode.ctx;
    if (tableInstance.has(id)) resetInstance(id);
  }
};
